::placeholder {
  color: #fff;
  opacity: .3;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

.col {
  float: left;
  width: 50%;
}

body {
  width: 100%;
  height: 100%;
}

header {
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.1);
  font-size: 13px;
  line-height: 18px;
  text-align: center;
}

main {
  padding: 10px 20px 0;
}

section {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 10px 0;
  margin-bottom: 10px;
}

section div {
  padding-bottom: 10px;
}

section div > label:not(.switch) {
  display: block;
  float: left;
  width: 240px;
  text-align: right;
  font-size: 13px;
  line-height: 20px;
  font-weight: 400;
  padding-right: 10px;
}

section div > label:not(.switch)::after {
  content: ':';
}

section div .switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 22px;
}

section div .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

section div .switch input:focus + .slider {
  box-shadow: 0 0 1px 2px rgba(52, 192, 249, 0.5);
}

section div .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 22px;
  transition: .25s;
}

section div .slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 2px;
  bottom: 2px;
  background-color: #fff;
  border-radius: 50%;
  transition: .25s;
}

section div input:checked + .slider {
  background-color: #34C0F9;
}

section div input:focus + .slider {
  box-shadow: 0 0 1px #34C0F9;
}

section div input:checked + .slider:before {
  transform: translateX(17px);
}

section div li label {
  font-size: 13px;
}

footer {
  padding: 10px 20px;
}

footer #close {
  display: block;
  width: 120px;
  height: 30px;
  margin-left: auto;
  background: rgba(3, 19, 32, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  font-size: 14px;
  color: #fff;
  font-weight: 200;
  text-align: center;
  transition: all .15s ease-in-out;
}

footer #close:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

footer #close:active {
  background-color: #020b12;
  box-shadow: inset 0 2px 4px #000204;
  outline: none;
}

footer #close:focus {
  outline: none;
  box-shadow: 0 0 1px 2px rgba(52, 192, 249, 0.5);
}
